import { Dialog, DialogContent, DialogTitle, Modal, Paper, TextField, Typography } from "@material-ui/core";
import * as React from "react";
import { useModalTheme } from "../../style";
import Defer from "../Defer";


const PinPrompt = ({ open, submitting, onClose, onAction, message = null }) => {
    const modalTheme = useModalTheme();
    const [pin, setPin] = React.useState('');

    const handlePin = (e) => {
        if (e.target.value.length >= 6) {
            onAction(e.target.value);
            setPin('');
        } else {
            setPin(e.target.value);
        }
    }
    return (
        <Dialog open={open} onClose={onClose} fullWidth>
            <DialogTitle>
                <Typography align="center" variant="h6">{message || 'Masukkan PIN'}</Typography>
            </DialogTitle>
            <DialogContent>
                <Defer data={!submitting} visible={open}>
                    {() => <>
                        <TextField type='password' inputProps={{ inputMode: 'numeric', style: { textAlign: 'center' } }}
                            value={pin} onChange={handlePin} fullWidth autoFocus />
                    </>}
                </Defer>
            </DialogContent>
        </Dialog>
    );
}

export default PinPrompt
