import { AppBar, Button, Divider, Grid, List, ListItem, ListItemIcon, ListItemText, Modal, Paper, Tab, Tabs, TextField, Typography } from "@material-ui/core";
import { navigate } from "gatsby";
import * as React from "react";
import ajax, { useFetcher } from "../../../components/ajax";
import { useModalTheme } from "../../../components/style";
import Defer from "../../../components/ui/Defer";
import PinPrompt from "../../../components/ui/market/PinPrompt";
import Navbar from "../../../components/ui/Navbar";
import { rupiah } from "../../../components/util";
// @ts-ignore
import icon from '../../../images/Icon Listrik.png';

const Prompt = ({ inquiry, open, onClose, onAction }) => {
    const modalTheme = useModalTheme();
    return (
        <Modal
            open={open}
            onClose={onClose}
        >
            {open && <Paper className={modalTheme.paper} elevation={5}><List>
                <ListItem>
                    <ListItemText primary={'No. Meter'} />
                    <ListItemText primary={inquiry.data.meterid} className={modalTheme.right} />
                </ListItem>
                <ListItem>
                    <ListItemText primary={'ID Pelanggan'} />
                    <ListItemText primary={inquiry.data.details.idpel} className={modalTheme.right} />
                </ListItem>
                <ListItem>
                    <ListItemText primary={'Nama'} />
                    <ListItemText primary={inquiry.data.details.nama} className={modalTheme.right} />
                </ListItem>
                <ListItem divider>
                    <ListItemText primary={'Tarif/Daya'} />
                    <ListItemText primary={inquiry.data.details.tarif} className={modalTheme.right} />
                </ListItem>
                <ListItem>
                    <ListItemText primary={'Nama Produk'} />
                    <ListItemText primary={inquiry.product.name} className={modalTheme.right} />
                </ListItem>
                <ListItem>
                    <ListItemText primary={'Harga'} />
                    <ListItemText primary={rupiah(inquiry.product.price)} className={modalTheme.right} primaryTypographyProps={{ variant: 'h6' }} />
                </ListItem>
                <ListItem>
                    <Button onClick={onClose} variant="outlined">Batal</Button>
                    <Button color="primary" variant="contained" style={{ marginLeft: 'auto' }} onClick={onAction}>Beli</Button>
                </ListItem>
            </List></Paper>}
        </Modal>
    );
}

const Page = () => {
    const [cust, setCust] = React.useState(null);
    const [prompt, setPrompt] = React.useState(0);
    const inquiry = React.useRef(null);
    const listToken = useFetcher(cust ? '/biller/pln/prepaid?meterid=' + cust : null, null, (err) => {
        alert(err.message);
        setCust(null);
    });

    const check = (e) => {
        e.preventDefault();
        setCust(e.currentTarget.account.value);
    }
    const confirm = (x) => {
        inquiry.current = {
            meterid: cust,
            code: x.code,
            pin: null,
            data: listToken,
            product: x
        };
        setPrompt(1);
    }
    const submit = (pin) => {
        inquiry.current.pin = pin;
        setPrompt(3);
        ajax.post('/biller/pln/prepaid', {
            meterid: inquiry.current.meterid,
            pin: inquiry.current.pin,
            code: inquiry.current.code,
        }).then(x => {
            alert('Berhasil');
            navigate('/digital/history/');
        }).catch(x => {
            alert(x.response?.data.message || x.message);
            setPrompt(2);
        });
    }
    return (
        <div>
            <Navbar />
            <form style={{ padding: '1em' }} onSubmit={check}>
                <List>
                    <ListItem>
                        <ListItemIcon><img src={icon} height="40px" alt="" /></ListItemIcon>
                        <ListItemText primary="Beli Listrik PLN Prabayar" />
                    </ListItem>
                    <ListItem>
                        <TextField name="account" label="Nomor ponsel" variant="filled" fullWidth />
                    </ListItem>
                    <ListItem>
                        <Button type="submit" variant="contained" color="primary">
                            Cek
                        </Button>
                        <Button style={{ marginLeft: 'auto' }} variant="outlined" onClick={() => navigate('/user/')}>
                            Kembali
                        </Button>
                    </ListItem>
                </List>
            </form>
            <Defer data={listToken} visible={cust}>
                {() => (
                    <Grid container spacing={2} style={{ padding: '1em' }}>{
                        listToken.prices.map(x => <Grid item xs={12} sm={6} key={x.code}>
                            <Button variant="outlined" fullWidth onClick={() => confirm(x)}>
                                <div style={{ textAlign: 'left', width: '100%' }}>
                                    <Typography variant="subtitle1">{x.name}</Typography>
                                    <Typography variant="subtitle2">{rupiah(x.price)}</Typography>
                                </div>
                            </Button></Grid>)
                    }</Grid>
                )}
            </Defer>
            <Prompt open={prompt === 1} onClose={() => setPrompt(0)} onAction={() => setPrompt(2)} inquiry={inquiry.current} />
            <PinPrompt open={prompt >= 2} submitting={prompt === 3} onClose={() => setPrompt(0)} onAction={submit} />
        </div >
    );
}

export default Page
